<template>
  <div class="app-container">
    <div class="filter-container">
      <el-input
        v-model="listQuery.name"
        placeholder="学生姓名"
        style="width: 200px;"
        class="mr10"
        @keyup.enter.native="handleFilter"
      />
      <el-button type="primary" icon="el-icon-search" @click="handleFilter">查询</el-button>
      <el-button type="primary" icon="el-icon-edit" @click="addHandler">添加学生</el-button>
    </div>

    <el-table
      key="studentList"
      v-loading="listLoading"
      :data="studentList"
      border
      fit
      highlight-current-row
      style="width: 100%; margin-top:15px; margin-bottom: 15px;">
      <!-- <el-table-column label="ID" prop="sid" sortable="custom" align="center" width="80">
        <template slot-scope="{row}">
          <span>{{ row.sid }}</span>
        </template>
      </el-table-column> -->
      <el-table-column label="学生姓名" width="110px" align="center">
        <template slot-scope="{row}">
          <span>{{ row.studentName||row.name }}</span>
        </template>
      </el-table-column>
      <el-table-column label="学历" width="100px">
        <template slot-scope="{row}">
          <span>{{ row.education }}</span>
        </template>
      </el-table-column>
      <el-table-column label="市场部" width="100px">
        <template slot-scope="{row}">
          <span>{{ row.orgName }}</span>
        </template>
      </el-table-column>
      <el-table-column label="联系电话" width="110px" align="center">
        <template slot-scope="{row}">
          <span>{{ row.tel }}</span>
        </template>
      </el-table-column>
      <el-table-column label="邮箱" width="110px" align="center">
        <template slot-scope="{row}">
          <span>{{ row.email }}</span>
        </template>
      </el-table-column>
      <el-table-column label="教师姓名" width="100px">
        <template slot-scope="{row}">
          <span>{{ row.teacherName }}</span>
        </template>
      </el-table-column>
      <el-table-column label="出生日期" align="center">
        <template slot-scope="scope">
          <span>{{ scope.row.birthday }}</span>
        </template>
      </el-table-column>
      <el-table-column label="备注" width="100px">
        <template slot-scope="{row}">
          <span>{{ row.notes }}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" align="center" width="230" class-name="small-padding fixed-width">
        <template slot-scope="{row}">
          <el-button type="primary" size="mini" @click="handleUpdate(row)">编辑</el-button>
          <el-button
            v-if="row.status!='deleted'"
            size="mini"
            type="danger"
            @click="handleDelete(row.sid)"
          >删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      class="mt15"
      background
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="listQuery.pageIndex"
      :page-size="listQuery.pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    ></el-pagination>

    <el-dialog :visible.sync="dialogFormVisible">
      <el-form :ref="textMap['dialogForm']" :model="ruleForm" :rules="rules" label-width="100px">
        <el-form-item label="姓名" prop="name">
          <el-input v-model.trim="ruleForm.name" placeholder="请输入学生姓名"></el-input>
        </el-form-item>
        <el-form-item label="性别" prop="sex">
          <el-radio-group v-model="ruleForm.sex">
            <el-radio :label="0">男</el-radio>
            <el-radio :label="1">女</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="身份证号" prop="idCard">
          <el-input v-model="ruleForm.idCard" placeholder="请输入学生身份证号码"></el-input>
        </el-form-item>
        <el-form-item label="出生日期" required>
          <el-date-picker
            style="width:100%"
            v-model="ruleForm.birthday"
            type="datetime"
            placeholder="出生日期"
            value-format="yyyy-MM-dd hh:mm:ss"
            format="yyyy-MM-dd hh:mm:ss"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="学历" prop="education">
          <!-- <el-input v-model="ruleForm.education" placeholder="请输入学生最高学历"></el-input> -->
          <el-select v-model="ruleForm.education" placeholder="请输入学生最高学历">
            <el-option value="初中">初中</el-option>
            <el-option value="高中">高中</el-option>
            <el-option value="大专">大专</el-option>
            <el-option value="本科">本科</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="电话" prop="tel">
          <el-input v-model="ruleForm.tel" placeholder="请输入学生联系方式"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="ruleForm.email" placeholder="请输入学生个人邮箱"></el-input>
        </el-form-item>
        <el-form-item label="地址" prop="place">
          <el-input v-model="ruleForm.place" placeholder="请输入学生现住址"></el-input>
        </el-form-item>
        <el-form-item label="家庭住址" prop="homeAddress">
          <el-input v-model="ruleForm.homeAddress" placeholder="请输入学生家庭住址"></el-input>
        </el-form-item>
        <el-form-item label="市场部" prop="orgCode">
          <!-- <el-select v-model="ruleForm.orgCode" placeholder="请选择市场部" style="width:100%">
            <el-option
              v-for="item in orgCodeList"
              :key="item.id"
              :label="item.orgName"
              :value="item.orgCode"
            ></el-option> 
          </el-select> -->
          <SelectOrg v-model="ruleForm.orgCode"/>
        </el-form-item>
        <el-form-item label="招生老师" prop="teacherId">
          <el-select v-model="ruleForm.teacherId" placeholder="请选择招生老师" style="width:100%">
            <el-option
              v-for="item in teacherList"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="备注" prop="notes">
          <el-input v-model="ruleForm.notes"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="onReset(textMap['dialogForm'])">重置</el-button>
        <el-button type="primary" @click="onSubmit(textMap['dialogForm'])">立即添加</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {
  getStudentList,
  deleteStudentById,
  getTeacherList,
  getOrgCodeList
} from "@/api/student";

import {
  textMap,
  calendarTypeKeyValue,
  validateAddRules,
  validateRules
} from "@/config/student";

import { parseTime, filterReset, resetForm } from "@/utils";
import {mapMutations, mapState} from 'vuex';
import SelectOrg from '@/components/SelectOrg';
import {addStudent} from '../../api/student';
export default {
  components: {
    SelectOrg
  },
  name: "Student",
  filters: {
    statusFilter(status) {
      const statusMap = {
        published: "success",
        draft: "info",
        deleted: "danger"
      };
      return statusMap[status];
    },
    typeFilter(type) {
      return calendarTypeKeyValue[type];
    }
  },
  data() {
    return {
      textMap,
      statusOptions: ["published", "draft", "deleted"],
      dialogFormVisible: false,
      listLoading: true,
      total: null,
      listQuery: {
        sid: "",
        pageSize: 5,
        pageIndex: 1,
        name: null
      },
      ruleForm: {
        name: "",
        idCard: "",
        birthday: "",
        education: "",
        tel: "",
        email: "",
        place: "",
        homeAddress: "",
        isRecommend: 0,
        notes: "",
        sex: null,
        orgCode: null,
        teacherId: null,
        orgName: null
      },
      rules: validateAddRules,
      studentList: [],
      teacherList: [],
      orgCodeList: []
    };
  },
  computed: {
    ...mapState('selectOrg', ['selectedNode'])
  },
  mounted() {
    this.handleFilter();
    this._getTeacherList();
    this._getOrgCodeList();
  },
  methods: {
    ...mapMutations('selectOrg', ['setState']),
    showSelectOrg(){
      this.setState({isSelectOrgShow: true});
    },
    async _getOrgCodeList() {
      const result = await getOrgCodeList();
      this.orgCodeList = result.data;
    },
    async _getTeacherList(orgCode) {
      const result = await getTeacherList(orgCode);
      this.teacherList = result.data.teachers;
    },
    async _getStudentList(params) {
      this.listLoading = true;
      const result = await getStudentList(params);
      this.listLoading = false;
      this.studentList = result.data.studentList;
      this.total = result.data.total;
    },
    async deleteStudent($id) {
      const params = {
        sid: $id
      };
      const result = await deleteStudentById(params);
      if (result.code == 1 && result.status === 201) {
        this._getStudentList();
      }
    },
    handleDelete($id) {
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.deleteStudent($id);
          this.$message({
            type: "success",
            message: "删除成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    handleSizeChange(size) {
      this.listQuery.pageSize = size;
      this.handleFilter();
    },
    handleCurrentChange(page) {
      this.listQuery.pageIndex = page;
      this.handleFilter();
    },
    handleFilter() {
      const params = filterReset(this.listQuery);
      this._getStudentList(params);
    },
    onReset(formName) {
      resetForm(this.ruleForm);
      this.$refs[formName].resetFields();
    },
    onSubmit(formName) {
      console.log(filterReset(this.ruleForm));
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$http
            addStudent(filterReset(this.ruleForm))
            .then(res => {
              if (res.code === 0) {
                return this.$message({
                  message: res.msg || "",
                  type: "error"
                });
              }
              this.dialogFormVisible = false;
              this.$message({
                message: res.msg || "",
                type: "success"
              });
              this._getStudentList();
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    addHandler() {
      this.dialogFormVisible = true;
    }
  },
  watch: {
    selectedNode (newVal){
      this.ruleForm.orgName = newVal.orgName;
      this.ruleForm.orgCode = newVal.orgCode;
    },
    'ruleForm.orgCode'(newVal){
      this._getTeacherList(newVal)
    }
  }
};
</script>

<style scoped>
.el-form-item {
  width: 50%;
  display: inline-block;
}
.mt15{
  text-align: right;
}
.mr10{
  margin-right: 10px;
}
</style>
