import { axios } from '@/utils/http';
import HOST from '@/config/host';
export function getStudentList(params) {
  const url = HOST + '/apply_student/getStudent';
  return axios.get(url, {
    params
  });
}

export function deleteStudentById(params) {
  const url = HOST + '/apply_student/delStudent';
  return axios.post(url, params);
}

export function addStudent(params) {
  const url = HOST + '/apply_student/addStudent';
  return axios.post(url, params);
}

export function getTeacherList(orgCode='10000') {
  const url = HOST + '/market_teacher/getTeacher';
  return axios.get(url, {params: {
    orgCode
  }});
}

export function getOrgCodeList() {
  const url = HOST + '/org_chart/getDepartment';
  return axios.get(url);
}
